.lol-bets-page {
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .lol-bets-page h1 {
    font-family: 'Fredoka', sans-serif;
    font-weight: 550;
    color: #ffffff;
    font-size: 39px;
    margin-bottom: 20px;
    padding-left: 85px;
  }
  
  .bets-list {
    display: flex;
    flex-direction: column;
    gap: 30px; 
    width: calc(100% - 85px); 
    padding-left: 85px;
  }
  
  .bet-item {
    background-color: #213743;
    padding: 20px;
    border-radius: 10px;
    color: #ffffff;
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 92.5%; 
  }
  
  .bet-date {
    font-size: 18px;
    font-weight: 550;
  }
  
  .bet-teams {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    width: 100%;
  }
  
  .team-info {
    display: flex;
    flex-direction: column;
    font-size: 20px;
    font-weight: 550;
    gap: 10px;
    flex-basis: 25%; 
    max-width: 25%; 
    justify-content: center;
  }
  
  .bet-odds-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    margin-left: 20px;
    flex-basis: 75%; 
    max-width: 75%; 
  }
  
  .bet-odds {
    display: flex;
    justify-content: space-between; 
    align-items: center;
    width: 100%;
    position: relative;
    cursor: pointer;
  }
  
  .winner {
    font-size: 16px;
    color: #c4dff6;
    position: absolute;
    top: -30px;
    left: 47%;
    transform: translateX(-50%);
    font-family: 'Fredoka', sans-serif;
    font-weight: 550;
  }
  
  .odds {
    display: flex;
    gap: 30px; 
    width: 95%;
  }
  
  .team-odds {
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1; /* Make the buttons grow to fill available space */
    max-width: calc(50% - 15px); /* Ensure the buttons take equal space */
  }
  
  .odds-box {
    background-color: #13232E;
    width: 100%; /* Set a flexible width for the box */
    padding: 10px; /* Set padding to only top and bottom to avoid vertical growth */
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    transition: background-color 0.3s, color 0.3s; /* Add transition for smooth effect */
  }
  
  .odds-box:hover {
    background-color: #b8860b; /* Darker gold on hover */
  }
  
  .team-odds.selected .odds-box {
    background-color: #e8c500; /* Gold on click */
    color: #ffffff; /* Change text color to white on click */
  }
  
  .team-odds.selected .team-name {
    color: #000000; /* Change team name to black on click */
  }
  
  .team-odds.selected .odds-value {
    color: white; /* Change team name to black on click */
  }
  
  .team-name {
    font-size: 18px;
    font-weight: 550;
  }
  
  .odds-value {
    font-family: 'Fredoka', sans-serif;
    font-weight: 550;
    font-size: 20px;
    color: inherit; /* Inherit color from parent */
    margin-top: 5px;
    color: #FFD700;
  }
  